html {
  --frontend_prog: 0deg;
  --backend_progress: 0deg;
  --dsa_prog: 0deg;
  --communication_prog: 0deg;
}
@property --frontend_prog {
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
}
@property --backend_progress {
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
}
@property --dsa_prog {
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
}
@property --communication_prog {
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
}
.skillsContainer {
  display: flex;
  /* width: 75%;
   */
  margin: 0 7.4rem;
  gap: 1rem;
  justify-content: space-around;
  color: white;
}
.card {
  display: flex;
  width: 240px;
  height: 360px;
  background-color: #000;
  border-radius: 10px;
  box-shadow: 0 0 20px #000;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.card:nth-child(1) .circle {
  background: conic-gradient(#f06529 0turn var(--frontend_prog), #202020 0turn);
}
.card:nth-child(2) .circle {
  background: conic-gradient(#f0f0f0 0turn var(--backend_progress), #202020 0turn);
}
.card:nth-child(3) .circle {
  background: conic-gradient(#f0f 0turn var(--dsa_prog), #202020 0turn);
}
.card:nth-child(4) .circle {
  background: conic-gradient(#f43212 0turn var(--communication_prog), #202020 0turn);
}
.circle {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 140px;
  height: 140px;
  text-align: center;
  border-radius: 50%;
  background-color: crimson;
  animation: animate 1.5s linear forwards;
}
.label {
  color: #bbb;
  font-size: 26px;
  font-weight: 600;
  margin-top: 30px;
}
@keyframes animate {
  100% {
    --frontend_prog: calc(calc(90 / 100) * 360deg);
    --backend_progress: calc(calc(70 / 100) * 360deg);
    --dsa_prog: calc(calc(70 / 100) * 360deg);
    --communication_prog: calc(calc(70 / 100) * 360deg);
  }
}

.counter {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
  position: absolute;
  width: 130px;
  height: 130px;
  border-radius: 50%;
  font-size: 26px;
}

@media screen and (max-width: 780px) {
  .skillsContainer {
    display: grid;
    grid-template-columns: repeat(2, auto);
    /* gap:10px */
  }
  .card {
    height: 240px;
  }
}

@media screen and (max-width: 500px) {
  .skillHeading {
    margin-bottom: 0.9rem;
  }
  .skillsContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* gap:10px */
  }
  .card {
    height: 200px;
  }
}
