.project-heading {
  text-align: center;
  padding: 5.1rem 0 2rem 0;
}
.project-container {
  max-width: 90%;

  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  padding-bottom: 5rem;
}
/* .pro-btn {
  margin-top: 15px;
} */
.project-card {
  background: #1a1919;
  width: 100%;
  /* height: 110%; */
  padding: 1.2rem 1rem;
}
.project-card img {
  width: 100%;
}
.project-title {
  color: #fff;
  padding: 1rem 0;
}
.pro-details p {
  padding-bottom: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}
.pro-btn {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.pro-btn .btn {
  padding: 0.5rem 1rem;
}
.tech-stack{
  color:#fff;
  padding: 0.5rem;
}
@media screen and (max-width:1080px) {
  .project-container{
    max-width: 90%;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 740px) {
  .project-container {
    max-width: 90%;
    margin: auto;
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 480px) {
  
  .pro-details p {
    font-size: 0.9rem;

  }
  .pro-btn .btn {
    font-size: 0.8rem !important;
  }
}
