.contactDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 0 2rem 0;
}
.contactDiv > div {
  display: flex;
  gap: 1rem;
  padding: 1rem;
  align-items: center;
}
.contactDiv > div > a {
  display: flex;
  gap: 1rem;

  align-items: center;
}
.contactDiv > div > h3,
.contactDiv > div > a > h3 {
  font-size: 24px;
  font-weight: 300;
}
.contactDiv > h1 {
  padding: 1.5rem 1rem 1rem 1rem;
font-weight: 500;
font-size: 38px;
}

@media screen and (max-width:760px) {
  .contactDiv > h1 {
    /* padding: 1.5rem 1rem 1rem 1rem; */
  font-weight: 500;
  font-size: 30px;
  }

  .contactDiv>div>h3,.contactDiv > div > a > h3{
    font-size: 16px;
  }
}