.TechHeader {
  text-align: center;
  margin: 60px 0 20px 0;
}
.TechStack {
  display: grid;
  grid-template-columns: repeat(6, auto);
  gap: 40px;
  margin: 2rem 10rem;
}

.TechStack > div {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.TechStack div:hover {
    
    transform: scale(1.1);
  }
@media screen and (max-width: 780px) {
  .TechStack {
    grid-template-columns: repeat(3, auto);
    margin: 2rem 4rem;
  }
}
@media screen and (max-width: 500px) {
  .TechStack {
    grid-template-columns: repeat(2, auto);
    gap: 20px;
    
  }
  .TechStack > div > label {
    padding: 0.2rem;
    font-size: 12px;
  }
}
