.gitDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.githubCalender {
  color: white;
  padding: 1rem;
  margin: 1rem;
  text-align: center;

}
.githubCalender h1{
    margin: 2rem 0;
}
.githubCalender h1>span{

    color: #00afb9;
}
.gitStats {
  display: flex;
  gap: 2rem;
  flex-direction: row;
  margin-bottom: 0.5rem; 
}
.gitStats h1 {
    font-weight: 400;
  padding: 1rem 0;
}

@media screen and (max-width: 760px) {
  .gitStats {
    gap: 0;
    flex-direction: column;
  }
  .gitStats > div {
    text-align: center;
  }
  .gitStats > div > img {
    height: 250px;
    width: 80%;
  }
  .gitStats h1 {
    font-size: 28px;
    font-weight: 400;
    padding: 0;
  }
}
