.about {
  padding: 6rem 0rem 2.5rem 0;  
}
.row {
  display: flex;
  align-items: center;
}
.about .left p {
  text-align: justify;
  margin: 20px 18%;
  font-size: 22px;
}
.about .left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about .right > img {
  margin-right: 7rem;
  width: 350px;
  height: fit-content;
  border-radius: 50%;

  border-width: 5px;
  border-style: solid;

  border-color: darkorchid;
}
/* .TechHeader {
  text-align: center;
  margin: 60px 0 20px 0;
}
*/
@media screen and (max-width: 780px) {
  .row {
    flex-direction: column;
  }
  /* .TechStack {
    grid-template-columns: repeat(3, auto);
    margin: 2rem 4rem;
  } */
  .about .left h1 {
    text-align: center;
    
    font-weight: 400;
    font-size: 20px;
  }
  .about .left p {
    text-align: justify;
    margin: 10px 14%;
    font-size: 16px;
  }
  .about .right > img {
    margin: 1.5rem 0 0 0;
  }
}
@media screen and (max-width: 600px) {
  /* .TechStack {
    grid-template-columns: repeat(2, auto);
  } */
  .about .right > img {
    margin: 1.5rem 0 0 0;
    width: 300px;
    height: 300px;
  }
}
